<template>
	<za-page-content header="专题管理">

		<za-table ref="table" :data="list" @action-change="changePage" highlight-current-row :total="total" @selection-change="handleSelectionChange">
			<div style="padding-bottom: 0px;" slot="searchBar">
				<el-form :model="filters" ref="filtersForm">
					<el-form-item>
						<el-col :span="8">
							<el-col :span="6">状态:</el-col>
							<el-col :span="16">
								<el-select style="width:100%" v-model="filters.status" placeholder="请选择">
									<el-option v-for="item in specialStatus" :key="item.value" :label="item.label" :value="item.value">
									</el-option>
								</el-select>
							</el-col>
						</el-col>
						<el-col :span="8">
							<el-col :span="6">专题样式:</el-col>
							<el-col :span="16">
								<el-select style="width:100%" v-model="filters.type" placeholder="请选择">
									<el-option v-for="item in subjectTypesWithAll" :key="item.value" :label="item.label" :value="item.value">
									</el-option>
								</el-select>
							</el-col>
						</el-col>
						<el-col :span="8">
							<el-col :span="6">所属栏目:</el-col>
							<el-col :span="16">
								<el-select style="width:100%" v-model="filters.directoryId" placeholder="请选择" :clearable="true">
									<el-option v-for="item in columnList" :key="item.id" :label="item.directoryName" :value="item.id">
									</el-option>
								</el-select>
							</el-col>
						</el-col>
					</el-form-item>
				</el-form>
			</div>

			<el-row slot="searchBar" style="margin-top: 30px">
				<el-button size="medium" icon="el-icon-search" @click="filter" type="primary">查询</el-button>
				<el-button size="medium" @click="addNewSpecial" type="success">创建专题</el-button>
			</el-row>

			<el-table-column prop="id" label="专题ID"></el-table-column>
			<el-table-column label="专题样式">
				<template slot-scope="scope">
					<el-col>{{parseType(scope)}}</el-col>
				</template>
			</el-table-column>
			<el-table-column prop="topicName" label="专题名称"></el-table-column>
			<el-table-column prop="directoryId" :formatter="formatterDirectoryName" label="所属栏目"></el-table-column>
			<el-table-column prop="articleNum" label="手动关联资讯数"></el-table-column>
			<el-table-column label="状态">
				<template slot-scope="scope">
					<el-col>{{statusText(scope)}}</el-col>
				</template>
			</el-table-column>
			<el-table-column label="创建时间">
				<template slot-scope="scope">
					{{scope.row.gmtCreated | date}}
				</template>
			</el-table-column>
			<el-table-column label="权重" prop="weight" width="60"></el-table-column>
			<el-table-column label="操作" width="140">
				<template slot-scope="scope">
					<el-button size="mini" type="primary" @click="previewArticle(scope.row)">预览</el-button>
					<el-button size="mini" type="primary" @click="editSpecial(scope.row)">编辑</el-button>
					<el-button v-if="scope.row.status=='01'" size="mini" type="danger" @click="changeStatus('offline', scope.row)">下线</el-button>
					<el-button v-if="scope.row.status=='02'" size="mini" type="success" @click="changeStatus('online', scope.row)">上线</el-button>
					<el-button size="mini" type="danger" @click="deleteSpecial(scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</za-table>

		<el-dialog title="添加专题" :visible.sync="dialogVisiblePage" @close="hideDialog" width="600px" top="5vh">
			<el-row>
				<el-row>
					<el-form :model="subForm" ref="form" :rules="formValidate" label-position="left" label-width="120px">
						<el-row type="flex" align="middle">
							<el-form-item label="专题样式" prop="type" style="width: 100%">
								<el-col :span="22">
									<el-select v-model="subForm.type" placeholder="请选择" style="width: 100%">
										<el-option v-for="item in subjectTypes" :key="item.value" :label="item.label" :value="item.value">
										</el-option>
									</el-select>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item label="所属栏目" prop="directoryId" style="width: 100%">
								<el-col :span="22">
									<el-select v-model="subForm.directoryId" placeholder="请选择" style="width: 100%">
										<el-option v-for="item in columnList" :key="item.id" :label="item.directoryName" :value="item.id">
										</el-option>
									</el-select>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle" v-if="subForm.type === '03'">
							<el-form-item label="开启评论" prop="isOpenComments" style="width: 100%">
								<el-col :span="22">
									<el-select v-model="subForm.isOpenComments" placeholder="是否开启评论" style="width: 100%">
										<el-option label="关闭" value="01"></el-option>
										<el-option label="开启" value="02"></el-option>
									</el-select>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item label="专题封面" prop="topicCover" style="width: 100%">
								<el-col :span="22">
									<div class="img-tip">* 建议宽高690:180</div>
									<el-upload class="avatar-uploader" :action="ajaxUrl.baseUrl + ajaxUrl.uploadArticleImage" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
										<img v-if="subForm.topicCover" :src="subForm.topicCover" class="avatar">
										<i v-else class="el-icon-plus avatar-uploader-icon"></i>
									</el-upload>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item label="专题名称" prop="topicName" style="width: 100%">
								<el-col :span="22">
									<div class="img-tip">* 少于40个字</div>
									<el-input v-model="subForm.topicName" :maxlength="40"></el-input>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle" v-if="subForm.type !=='02'">
							<el-form-item label="摘要" prop="topicAbstract" style="width: 100%">
								<el-col :span="22">
									<el-input type="textarea" :rows="2" placeholder="限40个字" v-model="subForm.topicAbstract"></el-input>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item label="状态" prop="status" style="width: 100%">
								<el-col :span="22">
									<el-select v-model="subForm.status" placeholder="请选择" style="width: 100%">
										<el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
										</el-option>
									</el-select>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item label="专题权重" prop="weight" style="width: 100%">
								<el-col :span="22">
									<el-input v-model="subForm.weight" type="number" placeholder="请输入0-100"></el-input>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item label="有效期" style="width: 100%">
								<el-col :span="22">
									<el-date-picker v-model="subForm.dateTimeRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" range-separator="-" start-placeholder="开始日期" end-placeholder="结束日期" align="right">
									</el-date-picker>
								</el-col>
							</el-form-item>
						</el-row>
						<el-row class="marginBottom" type="flex" align="middle">
							<el-col :span="15">专题资讯id（多条资讯使用英文逗号","隔开）</el-col>
						</el-row>
						<el-row type="flex" align="middle">
							<el-form-item prop="articleList" style="width: 100%">
								<el-col :span="22">
									<el-input type="textarea" :rows="12" placeholder="请输入内容" v-model="subForm.articleList"></el-input>
								</el-col>
							</el-form-item>
						</el-row>

						<el-row type="flex" align="middle" v-if="subForm.type !== '03'">
							<el-form-item label="动态更新标签:" prop="labels" style="width: 100%">
								<el-col :span="22">
									<el-select v-model="subForm.labelIds" filterable multiple remote :remote-method="debounceFunc(queryLabels, 300, false)" placeholder="请选择标签" style="width: 100%">
										<el-option v-for="(item, index) in labelList" :key="index" :label="item.labelName" :value="item.labelName">
										</el-option>
									</el-select>
								</el-col>
							</el-form-item>
						</el-row>

						<el-row type="flex" align="middle" v-if="subForm.type === '03'">
							<el-form-item label="功能组件ID:" style="width: 100%">
								<el-col :span="22">
									<el-input v-model="subForm.componentsIds" placeholder="多个组件用逗号隔开"></el-input>
								</el-col>
							</el-form-item>
						</el-row>

					</el-form>
				</el-row>
				<div class="dialog-footer" style="margin-top: 30px; text-align: center">
					<el-button @click="cancelDialog">取 消</el-button>
					<el-button @click="saveSpecial" type="primary">确 定</el-button>
				</div>
			</el-row>
		</el-dialog>
		<el-dialog :visible.sync="previewArticleData.isShow" width="30%">
			<img width="100%" :src="previewArticleData.qrBase64" alt="">
		</el-dialog>

	</za-page-content>
</template>

<script>
import { ajaxGetCommon, ajaxPostCommon } from "../../api/common.js";
import * as ajaxUrl from "../../api/url";
import { debounceFunc } from "@/utils/";
import { subjectTypesWithAll, subjectTypes } from "./components/staticData.js";

export default {
  data() {
    return {
      labelList: [],
      formValidate: {
        topicName: {
          required: true,
          message: "请输入专题名称",
          trigger: "blur"
        },
        topicAbstract: {
          required: true,
          message: "请输入专题摘要",
          trigger: "blur"
        },
        articleList: {
          required: false,
          message: '请输入文章列表(多条资讯使用英文逗号","隔开)',
          trigger: "blur"
        },
        topicCover: {
          required: true,
          message: "请上传专题封面",
          trigger: "blur"
        },
        weight: {
          required: true,
          message: "请填写权重",
          trigger: "blur"
        },
        status: { required: true, message: "请选择状态", trigger: "blur" }
      },
      ajaxUrl: ajaxUrl,
      uploadServer: ajaxUrl.uploadArticleImage,
      dialogVisiblePage: false,
      filters: {
        status: "",
        type: ""
      },
      specialStatus: [
        {
          label: "全部",
          value: ""
        },
        {
          label: "上线",
          value: "01"
        },
        {
          label: "下线",
          value: "02"
        }
      ],
      subForm: {
        topicName: "",
        topicAbstract: "",
        articleList: "",
        topicCover: "",
        status: "",
        weight: "",
        dateTimeRange: [],
        onlineDateStart: "",
        onlineDateEnd: ""
      },
      previewArticleData: {
        isShow: false,
        qrBase64: ""
      },
      options: [
        {
          value: "01",
          label: "上线"
        },
        {
          value: "02",
          label: "下线"
        }
      ],
      subjectTypesWithAll,
      subjectTypes,
      columnList: [],
      editSpecialId: null,
      list: [],
      imageUrl: "",
      total: 1,
      pageNum: 1,
      pageSize: 10
    };
  },
  created() {
    this.getColumnList();
  },
  methods: {
    debounceFunc,
    filter(status) {
      this.getList();
    },
    getList() {
      ajaxPostCommon(ajaxUrl.pageInfoTopic, {
        pageNum: this.pageNum,
        pageSize: this.pageSize,
        ...this.filters
      }).then(data => {
        if (data.returnCode === "200") {
          this.list = data.result.list;
          this.total = data.result.total;
        }
      });
    },
    getColumnList() {
      ajaxPostCommon(ajaxUrl.listInfoDirectory).then(res => {
				this.columnList = res.result;
				this.getList();
      });
    },
    saveSpecial() {
      let validatePass = false;
      this.$refs.form.validate(pass => {
        if (pass) validatePass = true;
      });
      if (!validatePass) {
        return false;
      }
      const submitData = this.ctrlSubmitData({ ...this.subForm });
      this.fetchSaveOrUpdate(this.editSpecialId, submitData);
    },

    ctrlSubmitData(submitData) {
      if (!!submitData.dateTimeRange && submitData.dateTimeRange.length > 0) {
        submitData.onlineDateStart = submitData.dateTimeRange[0];
        submitData.onlineDateEnd = submitData.dateTimeRange[1];
      }
      if (!!submitData.labelIds && submitData.labelIds.length > 0) {
        let temArr = [];
        submitData.labelIds.forEach(item => {
          temArr.push(item);
        });
        submitData.labelIds = temArr.join(",");
      }
      return submitData;
    },

    fetchSaveOrUpdate(editSpecialId, submitData) {
      let service = ajaxUrl.saveInfoTopic,
        successMessage = "保存成功",
        data = submitData;
      if (editSpecialId) {
        service = ajaxUrl.updateInfoTopic;
        data = Object.assign(submitData, { topicId: this.editSpecialId });
        successMessage = "更新成功";
      }
      ajaxPostCommon(service, data).then(data => {
        if (data.returnCode === "200") {
          this.getList();
          this.$message({
            message: successMessage,
            type: "success"
          });
          this.cancelDialog();
        } else {
          this.$message({
            message: data.returnMsg,
            type: "error"
          });
        }
      });
    },
    // 查询标签
    queryLabels(labelName) {
      if (!!labelName) {
        ajaxPostCommon(ajaxUrl.getLabels, { labelName }).then(res => {
          this.labelList = res.result;
        });
      } else {
        this.labelList = [];
      }
    },
    // 暂时废弃 需求不需要删除了
    deleteSpecial(scope) {
      this.$confirm("确定删除选中专题?", "提示", {})
        .then(() => {
          ajaxPostCommon(ajaxUrl.deleteInfoTopic, { topicId: scope.id }).then(
            data => {
              if (data.returnCode === "200") {
                this.cancelDialog();
                this.getList();
                this.$message({
                  message: "删除成功~",
                  type: "success"
                });
              } else {
              }
            }
          );
        })
        .catch(() => {});
    },

    changeStatus(type, row) {
      let status = type === "online" ? "01" : "02";
      ajaxPostCommon(ajaxUrl.updateTopicStatus, {
        topicId: row.id,
        status
      }).then(data => {
        if (data.returnCode === "200") {
          this.cancelDialog();
          this.getList();
          this.$message({
            message: "成功",
            type: "success"
          });
        } else {
        }
      });
    },

    editSpecial(scope) {
      ajaxPostCommon(ajaxUrl.getInfoTopicById, { topicId: scope.id }).then(
        data => {
          if (data.returnCode === "200") {
            let nowData = data.result;
            this.editSpecialId = nowData.id;
            if (!!nowData.labelIds) {
              nowData.labelIds = nowData.labelNames.split(",");
            }
            this.subForm = {
              topicName: nowData.topicName,
              topicAbstract: nowData.topicAbstract,
              articleList: nowData.articleList,
              topicCover: nowData.topicCover,
              status: nowData.status,
              weight: nowData.weight,
              type: nowData.type,
              labelIds: nowData.labelIds || [],
              directoryId: nowData.directoryId,
							isOpenComments: nowData.isOpenComments,
							componentsIds: nowData.componentsIds
            };
            if (!!nowData.onlineDateStart) {
              this.subForm = {
                ...this.subForm,
                onlineDateStart: new Date(nowData.onlineDateStart),
                onlineDateEnd: new Date(nowData.onlineDateEnd),
                dateTimeRange: [
                  new Date(nowData.onlineDateStart),
                  new Date(nowData.onlineDateEnd)
                ]
              };
            }
            this.openDialog();
          }
        }
      );
    },
    updateSpecial(submitData) {
      let params = Object.assign(submitData, { topicId: this.editSpecialId });
      ajaxPostCommon(ajaxUrl.updateInfoTopic, params).then(data => {
        if (data.returnCode === "200") {
          this.cancelDialog();
          this.getList();
          this.$message({
            message: "更新成功~",
            type: "success"
          });
        } else {
          this.$message({
            message: data.returnMsg,
            type: "error"
          });
        }
      });
    },
    addNewSpecial() {
      this.subForm = {
        topicName: "",
        topicAbstract: "",
        articleList: "",
        topicCover: "",
        weight: "",
				isOpenComments: "01",
				labelIds: []
      };
      this.editSpecialId = null;
      this.imageUrl = "";
      this.openDialog();
    },
    openDialog() {
      this.dialogVisiblePage = true;
    },
    hideDialog() {
			this.$refs.form.clearValidate();
    },
    cancelDialog() {
      this.dialogVisiblePage = false;
    },
    changePage(params) {
      this.pageNum = params.page;
      this.pageSize = params.rows || 10;
      this.getList();
    },
    beforeAvatarUpload() {},
    statusText(scope) {
      let obj = {
        "01": "上线",
        "02": "下线"
      };
      return obj[scope.row.status];
    },
    parseType(scope) {
      const result = subjectTypes.filter(item => item.value === scope.row.type);
      return result[0].label;
    },
    formatterDirectoryName(a, b, v) {
      const directoryItem = this.columnList.filter(
        item => item.id == v
      );
      return directoryItem.length > 0 ? directoryItem[0].directoryName : "未分配";
    },
    handleAvatarSuccess(res, file) {
      console.log(res, file);
      this.subForm.topicCover = res.result;
    },
    previewArticle(scope) {
      ajaxPostCommon(ajaxUrl.getTopicPreview, { topicId: scope.id }).then(
        data => {
          if (data.returnCode == 200) {
            this.previewArticleData.qrBase64 = `data:image/png;base64,${
              data.result
            }`;
            this.previewArticleData.isShow = true;
          } else {
            this.$message({
              showClose: true,
              message: data.returnMsg,
              type: "error"
            });
          }
        }
      );
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    handleSelectionChange(selection) {
      this.selectionRowData = selection;
    }
  }
};
</script>

<style lang="scss" scoped>
.targetTit {
  padding-top: 20px;
  margin-bottom: 20px;
}
.el-date-editor--datetimerange.el-input__inner {
  width: 100%;
}

.img-tip {
  color: red;
  position: absolute;
  left: -120px;
  top: 20px;
}

.el-button + .el-button {
  margin-left: 0;
  margin-top: 2px;
}
</style>



