/**
 * @desc 函数防抖
 * @param func 函数
 * @param wait 延迟执行毫秒数
 * @param immediate true 表立即执行，false 表非立即执行
 */
  export const debounceFunc = (func, wait, immediate) => {
    var timeout;
    return function () {
      var context = this;
      var args = arguments;

      if (timeout) clearTimeout(timeout);
      if (immediate) {
        var callNow = !timeout;
        timeout = setTimeout(function () {
          timeout = null;
        }, wait)
        if (callNow) func.apply(context, args)
      }
      else {
        timeout = setTimeout(function () {
          func.apply(context, args)
        }, wait);
      }
    }
  }

/**
 * @desc 函数节流
 * @param func 函数
 * @param wait 延迟执行毫秒数
 * @param type 1 表时间戳版，2 表定时器版
 */
  export const throttleFunc = (func, wait, type) => {
    if (type === 1) {
      var previous = 0;
    } else if (type === 2) {
      var timeout;
    }

    return function () {
      var context = this;
      var args = arguments;
      if (type === 1) {
        var now = Date.now();

        if (now - previous > wait) {
          func.apply(context, args);
          previous = now;
        }
      } else if (type === 2) {
        if (!timeout) {
          timeout = setTimeout(function () {
            timeout = null;
            func.apply(context, args)
          }, wait)
        }
      }

    }
  }



